<template>
    <div class="container back-one">
        <div class="withdrawal-pass">
            <ul>
                <li>
                    <div class="label">旧密码：</div>
                    <input v-model="oldPassword" maxlength="22" type="password" placeholder="请输入旧密码">
                </li>
                <li>
                    <div class="label">新密码：</div>
                    <input v-model="newPassword" maxlength="22" type="password" placeholder="请输入新密码">
                </li>
            </ul>
            <div class="tips">
                <span>密码由<i>6-16</i>个字符组成，区分大小写 ( 不能是9位以下纯数字，不能包含空格 ) 为了提升您的安全性，建议使用<i>英文加数字</i>或者<i>混合组合密码</i></span>
            </div>
            <ul>
                <li>
                    <div class="label">确认密码：</div>
                    <input v-model="newPasswordc" maxlength="22" type="password" placeholder="确认新密码">
                </li>
            </ul>
            <div class="public-btn mt25">
                <button @click="modify" class="button">提交</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                oldPassword: '',
                newPassword: '',
                newPasswordc: '',
            }
        },
        created(){
            var user = this.$user()
            this.$store.commit('User/SET_USER', user)
        },
        methods: {
            modify(){
                if(this.newPassword != this.newPasswordc){
                    this.$toast('两次密码不一致！')
                }
                var oldPassword = this.$md5(this.oldPassword)
                var newPassword = this.$md5(this.newPassword)
                let data = {
                    ...this.baseUser,
                    oldPassword,
                    newPassword,
                }
                this.$api.user.resetPassword(data)
                .then(res=>{
                    console.log(res);
                    if(res.result==1){
                        this.$toast('修改成功')
                    }else{
                        this.$toast(res.description)
                    }
                })
            }
        }
    }
</script>

